import { defHttp } from '/@/utils/http/axios';

enum Api {
  //设备统计
  getDeviceStatUrl = '/da/group-operation/monitor/deviceStat',

  //发电排行
  getPowerRankUrl = '/da/group-operation/monitor/powerRank',

  //储能监视

  //顶部指标
  getTopInfoUrl = '/da/group-operation/monitor/topInfo',

  //告警总览
  getWarningInfoUrl = '/da/group-operation/monitor/warning/info',

  //离散数据分析
  getDispersedRateUrl = '/monitor/large-screen/station/dispersionRatio',

  //发电趋势
  getPowerTrendUrl = '/da/group-operation/monitor/powerTrend',
}
export const getDeviceStatData = (params) => defHttp.get({ url: Api.getDeviceStatUrl, params }, {});

export const getPowerRankData = (params) => defHttp.get({ url: Api.getPowerRankUrl, params }, {});

export const getTopInfoData = (params) => defHttp.get({ url: Api.getTopInfoUrl, params }, {});

export const getWarningInfoData = (params) =>
  defHttp.get({ url: Api.getWarningInfoUrl, params }, {});

export const getDispersedRateData = (params) =>
  defHttp.get({ url: Api.getDispersedRateUrl, params }, {});

export const getPowerTrendData = () => defHttp.get({ url: Api.getPowerTrendUrl }, {});

